body.page-template-page_trainer_landing
{
		padding-top:0;
	&.no-padding
	{
		padding-top:0;
	}

	h2
	{
		font-family:@font-family-headings;
		color:@brand-secondary;
		text-shadow:1px 1px 0 darken(@brand-secondary,10%), -1px -1px 0 @brand-primary;

		@media (max-width:@screen-xxs-max)
		{
			font-size:24px;
		}
	}

	.home-cover
	{
		background-image:url('../img/photos/my_gym.jpg');
		background-position:50% 50%;
		background-size:cover;

		margin-bottom:(2 * @line-height-computed);

		@media (orientation:portrait)
		{
			background-image:url('../img/photos/my_gym.jpg');
			background-position:100% 50%;
		}

		.home-cover-col
		{
			min-height:100vh;

			display:flex;
			flex-direction:column;
			justify-content:space-between;
			align-items:center;

			.title-section
			{
				padding-top:@line-height-computed;

				h1
				{
					text-align:center;
					color:@gray-lighter;
					text-shadow:2px 2px 0 #000,-1px -1px 0 #aaa;
					font-family:@font-family-headings;

					@media (max-width:@screen-xxs-max)
					{
						font-size:28px;
					}
				}

				img
				{
					display:block;
					margin-left:auto;
					margin-right:auto;
					margin-top:40px;

					width:40%;
					max-width:200px;
				}
			}

			.subtitle-section
			{
				padding-bottom:(2 * @line-height-computed);
				padding-left:ceil((@grid-gutter-width / 2));
				padding-right:floor((@grid-gutter-width / 2));

				h2
				{
					text-align:center;
					font-family:@font-family-headings;
					color:@gray-lighter;
					text-shadow:1px 1px 0 #000,-1px -1px 0 #aaa;

					@media (max-width:@screen-xxs-max)
					{
						font-size:24px;
					}
				}

				.links
				{
					display:flex;
					justify-content:space-around;
					align-items:center;

					@media (orientation:portrait)
					{
						flex-direction:column;
					}

					a
					{
						display:inline-block;

						margin:@line-height-computed;
						padding:ceil((@line-height-computed / 2)) @line-height-computed;
						border:2px solid @gray-lighter;

						font-family:@font-family-headings;
						font-size:24px;
						color:@gray-lighter;
						background-image:linear-gradient(45deg, rgba(0,0,0,.5) 0, rgba(0,0,0,.8) 50%, rgba(0,0,0,.5) 100%);

						text-align:center;

						&:hover, &:focus
						{
							//box-shadow:0 0 6px 0 @white;
							text-decoration:none;
							box-shadow:0 0 6px 0 @brand-primary;
							color:@brand-primary;
							border:2px solid lighten(@brand-primary,10%);
						}

						@media (orientation:portrait)
						{
							width:100%;
							margin:@line-height-computed 0;
						}

						@media (max-width:@screen-xxs-max)
						{
							font-size:20px;
						}
					}
				}
			}
		}
	}

	.full-custom
	{
		h2
		{
			text-align:center;
		}
		h3
		{
			text-align:center;
			margin-bottom:(2 * @line-height-computed);
		}
		ul
		{
			margin-bottom:(2 * @line-height-computed);
		}
	}

	.chose
	{
		background-size:cover;
		background-position:50% 50%;
		background-image:url('../img/photos/training.jpg');

		margin-bottom:(2 * @line-height-computed);

		@media (orientation:portrait)
		{
			background-position:25% 50%;
		}

		.chose-col
		{
			min-height:100vh;
			display:flex;
			flex-direction:column;
			justify-content:space-between;
			align-items:center;

			h2
			{
				margin-top:(3 * @line-height-computed);
				text-align:center;
				font-family:@font-family-headings;
				color:@gray-lighter;
				text-shadow:1px 1px 0 #000,-1px -1px 0 #aaa;

				@media (max-width:@screen-xxs-max)
				{
					font-size:24px;
				}
			}

			h3
			{
				text-align:center;
				color:@gray-lighter;
				text-shadow:1px 1px 0 #000;

				@media (max-width:@screen-xxs-max)
				{
					font-size:20px;
				}
			}

			.links
			{
				display:flex;
				justify-content:space-around;
				align-items:center;

				@media (orientation:portrait)
				{
					flex-direction:column;
				}

				a
				{
					display:inline-block;

					margin:@line-height-computed;
					margin-bottom:(3 * @line-height-computed);
					padding:ceil((@line-height-computed / 2)) @line-height-computed;
					border:2px solid @gray-lighter;

					font-family:@font-family-headings;
					font-size:24px;
					color:@gray-lighter;
					background-image:linear-gradient(45deg, rgba(0,0,0,.5) 0, rgba(0,0,0,.8) 50%, rgba(0,0,0,.5) 100%);

					text-align:center;

					&:hover, &:focus
					{
						//box-shadow:0 0 6px 0 @white;
						text-decoration:none;
						box-shadow:0 0 6px 0 @brand-primary;
						color:@brand-primary;
						border:2px solid lighten(@brand-primary,10%);
					}

					@media (orientation:portrait)
					{
						width:100%;
						margin:@line-height-computed 0;
					}

					@media (max-width:@screen-xxs-max)
					{
						font-size:20px;
					}
				}
			}

		}
	}

	.technology
	{
		margin-bottom:(2 * @line-height-computed);

		img
		{
			width:80%;
			max-width:300px;
			display:block;
			margin:@line-height-computed auto;
		}

		h2
		{
			text-align:center;
		}
		h3
		{
			text-align:center;
		}
	}
	
	.live
	{
		margin-bottom:(2 * @line-height-computed);

		h2
		{
			text-align:center;
		}
		h3
		{
			text-align:center;
			margin-bottom:(2 * @line-height-computed);
		}
	}

}

