.cool-big-list
{
    list-style: none;
    padding-left: 0;
	font-size:22px;

	@media (max-width:@screen-xxs-max)
	{
		font-size:18px;
	}

	li
	{
		position: relative;
		padding-left: 40px;
		margin-bottom:@line-height-computed;

		@media (max-width:@screen-xxs-max)
		{
			padding-left:30px;
		}

		&:before
		{
			content: '';
			width: 30px;
			height: 30px;
			position: absolute;
			background-image: url('../img/logo.png');
			background-size: cover;
			background-position: center;
			left: 0;
			top: 50%;
			transform: translateY(-50%);

			@media (max-width:@screen-xxs-max)
			{
				width: 20px;
				height: 20px;
			}
		}
	}
}
