body.page-template-page_client_landing
{
		padding-top:0;
	&.no-padding
	{
		padding-top:0;
	}

	h2
	{
		font-family:@font-family-headings;
		color:@brand-secondary;
		text-shadow:1px 1px 0 darken(@brand-secondary,10%), -1px -1px 0 @brand-primary;

		@media (max-width:@screen-xxs-max)
		{
			font-size:24px;
		}
	}

	.home-cover
	{
		background-image:url('../img/photos/my_gym.jpg');
		background-position:50% 50%;
		background-size:cover;

		margin-bottom:(2 * @line-height-computed);

		@media (orientation:portrait)
		{
			background-image:url('../img/photos/my_gym.jpg');
			background-position:100% 50%;
		}

		.home-cover-col
		{
			min-height:100vh;

			display:flex;
			flex-direction:column;
			justify-content:space-between;
			align-items:center;

			.title-section
			{
				padding-top:@line-height-computed;

				h1
				{
					text-align:center;
					color:@gray-lighter;
					text-shadow:2px 2px 0 #000,-1px -1px 0 #aaa;
					font-family:@font-family-headings;

					@media (max-width:@screen-xxs-max)
					{
						font-size:28px;
					}
				}

				img
				{
					display:block;
					margin-left:auto;
					margin-right:auto;
					margin-top:40px;

					width:40%;
					max-width:200px;
				}
			}

			.subtitle-section
			{
				padding-bottom:(2 * @line-height-computed);
				padding-left:ceil((@grid-gutter-width / 2));
				padding-right:floor((@grid-gutter-width / 2));

				max-width:100%;

				h2
				{
					text-align:center;
					font-family:@font-family-headings;
					color:@gray-lighter;
					text-shadow:1px 1px 0 #000,-1px -1px 0 #aaa;

					@media (max-width:@screen-xxs-max)
					{
						font-size:24px;
					}
				}

				.links
				{
					display:flex;
					justify-content:space-around;
					align-items:center;

					@media (orientation:portrait)
					{
						flex-direction:column;
					}

					@media (orientation:landscape) and (max-width:1020px)
					{
						flex-wrap:wrap;
						justify-content:center;
					}

					a
					{
						display:inline-block;

						margin:@line-height-computed;
						padding:ceil((@line-height-computed / 2)) @line-height-computed;
						border:2px solid @gray-lighter;

						font-family:@font-family-headings;
						font-size:24px;
						color:@gray-lighter;
						background-image:linear-gradient(45deg, rgba(0,0,0,.5) 0, rgba(0,0,0,.8) 50%, rgba(0,0,0,.5) 100%);

						text-align:center;

						&:hover, &:focus
						{
							box-shadow:0 0 6px 0 @white;
							text-decoration:none;
						}

						@media (orientation:portrait)
						{
							width:100%;
							margin:@line-height-computed 0;
						}

						@media (max-width:1020px)
						{
							font-size:20px;
							margin:ceil((@line-height-computed / 2));
							padding:ceil((@line-height-computed / 4)) ceil((@line-height-computed / 2));
						}

						@media (max-width:@screen-xxs-max)
						{
							font-size:20px;
							margin:@line-height-computed;
							padding:ceil((@line-height-computed / 2)) @line-height-computed;
						}
					}
				}
			}
		}
	}

	.choose-trainer
	{
		margin-top:40px;
		margin-bottom:40px;

		.choose-col
		{
			padding:0;

			.item
			{
				min-height:100vh;

				background-image:linear-gradient(45deg, @brand-primary 0, @brand-secondary 100%);


				.trainer-item-layer
				{
					display:flex;
					align-items:stretch;
					flex-wrap:wrap;
					min-height:100vh;

					@media (orientation:portrait)
					{
						flex-wrap:wrap;
					}

					.siteheader
					{
						width:100%;
						display:flex;
						align-items:center;
						min-height:15vh;
						padding:20px;

						.logo-layer
						{
							.blog-logo
							{
								max-width:100px;
								max-height:90px;
							}
						}
						.header-text
						{
						}
					}

					.photo
					{
						min-height:85vh;
						width:50%;
						background-image:url('../img/silueta.png');
						background-size:auto 100%;
						background-position:50% 0;
						background-repeat:no-repeat;

						position:relative;

						&.woman
						{
							background-image:url('../img/silueta_mujer.png');
						}

						@media (orientation:portrait)
						{
							width:100%;
							order:2;
							min-height:50vh;
						}

						

						.valoration
						{
							position:absolute;
							left:50%;
							bottom:20%;
							transform:translate(-50%, 0);

							display:flex;
							flex-direction:column;
							align-items:center;
							justify-content:flex-start;

							.stars
							{

								height:50px;
								width:0;

								background-image:url('../img/star.svg');
								background-size:50px 100%;
								background-repeat:repeat;

								margin-bottom:20px;
							}
						}

					}

					.text
					{
						width:50%;

						display:flex;
						flex-direction:column;
						align-items:center;
						justify-content:center;

						@media (orientation:portrait)
						{
							width:100%;
							order:1;
						}

						.subtitle
						{
							margin-bottom:30px;
							font-weight:700;
						}

						.prices
						{
							display:flex;

							.price
							{
								font-size:24px;
								margin-left:10px;
								margin-right:10px;
								text-align:center;
								line-height:1;

								small
								{
									font-size:16px;
								}
							}

						}

						.knowledge
						{
							padding:15px;
							margin-top:10px;
							margin-bottom:20px;

							width:90%;
							max-width:600px;
							min-width:300px;

							background-color:fade(#000,20%);

							h4
							{
								text-align:center;
								margin-top:0;
							}

							p
							{
								margin-bottom:2px;
							}

							.bar
							{
								position:relative;

								width:100%;
						
								margin-bottom:10px;

								border-radius:2px;
								height:10px;

								box-shadow:inset 0 1px 2px 0 #000;

								.inner
								{
									background-color:@brand-primary;
									height:10px;
									box-shadow:0 0 3px 0 lighten(@brand-primary, 5%);
								}
							}
						}

/*
						.valoration
						{
							padding:5px;
							background-color:gold;
							text-align:center;
							border-radius:8px;
							box-shadow:2px 2px 1px 0 #666;

							.value
							{
								color:#cc2222;
								text-shadow:1px 1px 0 #222;
								padding-bottom:3px;
								font-size:38px;
							}

						}
*/
						.valoration
						{
							display:flex;
							flex-direction:column;
							align-items:center;
							justify-content:flex-start;

							.stars
							{

								height:50px;
								width:0;

								background-image:url('../img/star.svg');
								background-size:50px 100%;
								background-repeat:repeat;

								margin-bottom:20px;
							}
						}
					}
				}
			}
		}
		
	}

	.trainers-list-container
	{
		.trainers-list-col
		{
			.trainers-list
			{
				display:flex;
				justify-content:flex-start;
				align-items:stretch;
				flex-wrap:wrap;

				.trainer-list-element
				{
					width:33.33%;
					padding:15px;
					text-align:center;

					@media(max-width:@screen-xs-max)
					{
						width:50%;
					}
					@media(max-width:@screen-xxs-max)
					{
						width:100%;
					}

					.trainer-list-element-inner
					{
						background-color:#000;
						padding:10px;
						box-shadow:1px 1px 1px 0 #333;
						border-radius:10px;

						.title h3
						{
							margin-top:5px;
						}

						img.blog-logo
						{
							max-width:100%;
							max-height:80px;
							margin-bottom:0;

							margin-left:auto;
							margin-right:auto;
							display:block;
						}

						.photo
						{
							width:100%;
							height:200px;
							background-image:url('../img/silueta.png');
							background-size:auto 100%;
							background-position:50% 0;
							background-repeat:no-repeat;

							&.woman
							{
								background-image:url('../img/silueta_mujer.png');
							}
						}

						.prices
						{
							display:flex;
							justify-content:center;
							align-items:center;

							.price
							{
								color:#fff;
								font-size:16px;
								margin-left:10px;
								margin-right:10px;
								text-align:center;
								line-height:1;

								small
								{
									font-size:12px;
								}
							}
						}

						.valoration
						{
							display:flex;
							flex-direction:column;
							align-items:center;
							justify-content:flex-start;

							.stars
							{

								height:30px;
								width:0;

								background-image:url('../img/star.svg');
								background-size:30px 100%;
								background-repeat:repeat;

							}

							.valorations-link
							{
								color:#ff;
								font-size:13px;
								cursor:pointer;
							}
						}

						.knowledge
						{
							padding:5px;
							margin-top:5px;
							margin-bottom:5px;

							width:100%;

							background-color:fade(#000,20%);

							p
							{
								margin-bottom:2px;
								color:#fff;
							}

							.bar
							{
								position:relative;

								width:100%;
						
								margin-bottom:10px;

								border-radius:2px;
								height:10px;

								box-shadow:inset 0 1px 2px 0 #000;

								.inner
								{
									background-color:@brand-primary;
									height:10px;
									box-shadow:0 0 3px 0 lighten(@brand-primary, 5%);
								}
							}
						}

						a
						{
							color:#fff;
						}
					}
				}
			}
		}
	}

	.trainer-filter-container
	{
		background-image:linear-gradient(90deg,@brand-primary 0, lighten(@brand-primary,5%) 50%, @brand-primary 100%);

		.trainer-filters
		{
			display:flex;
			flex-wrap:wrap;

			.filters-title
			{
				width: 100%;
				text-align: center;
				padding-top: 20px;
			}

			.trainer-filter
			{
				width:25%;
				padding:20px 20px 20px 35px;

				&.slider-filter
				{
					padding-left:35px;
					padding-right:35px;

					@media (max-width:@screen-xxs-max)
					{
						padding-left:20px;
						padding-right:20px;
					}
				}

				@media (max-width:@screen-xs-max)
				{
					width:50%;
				}

				h5
				{
					font-size:13px;
				}

				.ui-slider
				{
					margin-top:20px;
					.ui-slider-handle
					{
						top:-10px;
						padding-top:3px;
						width:30px;
						height:30px;
						border-radius:100%;
						border:2px solid #eee;
						background-color:#000;
						color:#d8d648;
						font-weight:bold;
						text-align:center;
						text-shadow:0 0 2px #e8e658;
						box-shadow:-1px -1px 0 0 #fff, 1px 1px 1px 0 #666;
					}
				}
			}
		}

	}

	.full-custom
	{
		h2
		{
			text-align:center;
		}
		h3
		{
			text-align:center;
			margin-bottom:(2 * @line-height-computed);
		}
		ul
		{
			margin-bottom:(2 * @line-height-computed);
		}
	}

	.chose
	{
		background-size:cover;
		background-position:50% 50%;
		background-image:url('../img/photos/training.jpg');

		margin-bottom:(2 * @line-height-computed);

		@media (orientation:portrait)
		{
			background-position:25% 50%;
		}

		.chose-col
		{
			min-height:100vh;
			display:flex;
			flex-direction:column;
			justify-content:space-between;
			align-items:center;

			h2
			{
				margin-top:(3 * @line-height-computed);
				text-align:center;
				font-family:@font-family-headings;
				color:@gray-lighter;
				text-shadow:1px 1px 0 #000,-1px -1px 0 #aaa;

				@media (max-width:@screen-xxs-max)
				{
					font-size:24px;
				}
			}

			h3
			{
				text-align:center;
				color:@gray-lighter;
				text-shadow:1px 1px 0 #000;

				@media (max-width:@screen-xxs-max)
				{
					font-size:20px;
				}
			}

			.links
			{
				display:flex;
				justify-content:space-around;
				align-items:center;

				@media (orientation:portrait)
				{
					flex-direction:column;
				}

				a
				{
					display:inline-block;

					margin:@line-height-computed;
					margin-bottom:(3 * @line-height-computed);
					padding:ceil((@line-height-computed / 2)) @line-height-computed;
					border:2px solid @gray-lighter;

					font-family:@font-family-headings;
					font-size:24px;
					color:@gray-lighter;
					background-image:linear-gradient(45deg, rgba(0,0,0,.5) 0, rgba(0,0,0,.8) 50%, rgba(0,0,0,.5) 100%);

					text-align:center;

					&:hover, &:focus
					{
						box-shadow:0 0 6px 0 @white;
						text-decoration:none;
					}

					@media (orientation:portrait)
					{
						width:100%;
						margin:@line-height-computed 0;
					}

					@media (max-width:@screen-xxs-max)
					{
						font-size:20px;
					}
				}
			}

		}
	}

	.technology
	{
		margin-bottom:(2 * @line-height-computed);

		img
		{
			width:80%;
			max-width:300px;
			display:block;
			margin:@line-height-computed auto;
		}

		h2
		{
			text-align:center;
		}
		h3
		{
			text-align:center;
		}
	}
	
	.live
	{
		margin-bottom:(2 * @line-height-computed);

		h2
		{
			text-align:center;
		}
		h3
		{
			text-align:center;
			margin-bottom:(2 * @line-height-computed);
		}
	}


}


