.navbar-default
{
	&.transparent
	{
		background-image:none;
		background-color:transparent;
		border-color:transparent;

		.navbar-collapse
		{
			&.collapsing, &.in
			{
				background-color:#000;
			}
		}
	}

	.navbar-brand
	{
		padding-top:10px;

		img.logo
		{
			width:40px;
			float:left;
			border-radius:100%;
		}

		.site-text
		{
			float: left;
			display: inline-block;
			margin-top: 5px;
			margin-left: 10px;
			font-size:40px;

			@media (max-width:@screen-xs-max)
			{
				font-size:22px;
				margin-top:9px;
			}
		}

		&:hover
		{
			img.logo
			{
				box-shadow:0 0 5px 2px @brand-primary;
			}
			.site-text
			{
				text-shadow: 1px 1px 10px @brand-primary,5px 5px 10px @brand-primary,-5px -5px 10px @brand-primary;
			}
		}
	}
}
