.basic-contact-layer()
{
	//background-color:#fff;
	//background-repeat:no-repeat;
	//background-position:50% 0;
	
	//box-shadow:0 -2px 4px 0 @gray;

	& > .row
	{
		//background-color:@background-color-2;
		//#gradient .vertical(darken(@background-color-2,8%),@background-color-2);

		& > .col-xs-12
		{
		//	padding-bottom:@line-height-computed;
		}
	}

	.contact-flex-row
	{
		display:flex;
		flex-wrap:wrap;

		.contact
		{
			display:flex;
			flex-direction:column;
			align-items:center;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				order:2;
				padding:0;
			}
		}

		.img
		{
			display:flex;
			align-items:flex-end;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				img
				{
					width:100%;
					max-width:300px;
					margin-bottom:(@line-height-computed / 2);
				}

				order:1;
			}
		}
	}

	h1,h2,h3
	{
		//color:@white;
		//text-shadow:1px 1px 0 #aa8f7d; 
	}

	form
	{
		width:100%;
		padding:(@line-height-computed / 2);

		background-image: linear-gradient(to top, #bfbdb9 0, #dadbd8 25%, #fdfdfc 50%, #dadbd8 75%, #e6e7e5 100%);
		margin-top: 20px;
		margin-bottom: 20px;
		border-radius: 12px;
		box-shadow: 2px 2px 2px 0 #111;
		border: 2px solid #80817d;

		label
		{
			color: #000;
			text-shadow: -1px -1px 0 #eeeeec,1px 1px 0 #666;
		}


		.form-control
		{
			box-shadow: inset 2px 2px 2px 0 #666;
			border-radius: 6px !important;
			background-color: green;
			border-style: solid;
			background-image: linear-gradient(to bottom, #9cc242 0, #d5d237 50%, #9cc242 100%);
			font-size: 20px;
			border-width: 1px;
			border-color: #eeeeec #c1bfbc #c5c4c0 #eeeeed;
		}

		textarea.form-control
		{
			height: 138px;
		}
		input.form-control
		{
			height: 46px;
		}

		a
		{
			color:@white;
			font-weight:bold;

			&:hover
			{
				//color:lighten(@brand-primary,30%);
			}
		}

		button
		{
			//margin-top:10px;
			//width:100%;
			//.btn-styles(@background-color-2);
			//color:@white;

			display:block;
			margin:@line-height-computed auto;
			font-size:24px;
			//.btn-styles(#21b72e);
			border-color: #666;
			border-radius: 12px;
			height: 50px;
			padding-left: 20px;
			padding-right: 20px;
			background-image: linear-gradient(to top, #bfbdb9 0, #dadbd8 25%, #fdfdfc 50%, #dadbd8 75%, #e6e7e5 100%);
			color: #a1c340;
			text-shadow: 1px 1px 0 #80817d, -1px -1px 0 #fafaf8;
			font-weight: bold;

			&:hover, &:focus, &:active
			{
				//color:@white;
			}
		}

	}

	.map-col
	{
		padding:0;
		
		iframe
		{
			width:100%;
		}
	}
	
}

.contact-info-container
{
	& > .row
	{
		padding-top:@line-height-computed;
		padding-bottom:@line-height-computed;
	}

	.contact-icon
	{
		width:80%;
		max-width:300px;
		margin-top:@line-height-computed;
	}

	a
	{
		color:@brand-secondary;
		text-shadow:1px 1px 0 darken(@brand-secondary,10%);

		margin-bottom:@line-height-computed;

		&:hover
		{
			text-decoration:none;
			text-shadow:0 0 3px @brand-primary;
		}

		h3
		{
			font-size:18px;
			word-break:break-all;
		}
	}
}

.contact-container
{
	.basic-contact-layer();

}


