.main-footer
{
 	//#gradient > .vertical(@start-color: @navbar-default-bg; @end-color: lighten( @brand-primary, 40%) );
	background-color:#000;//@brand-secondary;
	border-top:1px solid #222;
	color:@white;

	padding-top:@line-height-computed;

	a
	{
		color:@brand-primary;

		&:hover
		{
			color:lighten( @brand-primary, 20% );
		}
	}

	.logo-link
	{
		img
		{
			width:100%;
		}
	}

	.footer-legal
	{
		.footer-legal-col
		{
			display:flex;
			justify-content:flex-start;
			align-items:center;

			img
			{
				width:136px;
				margin-right:20px;
				margin-bottom:10px;
			}

			@media (max-width:@screen-xxs-max)
			{
				flex-wrap:wrap;

				img
				{
					width:100%;
				}
			}
		}
	}
}
